import './MessagesChatBlock.css';
import {useLocation, useNavigate} from "react-router";
import {parsingURL} from "../../MainHomeUsers/parsingURL";
import {useEffect} from "react";
import {ThunkTakeFirstPageChat, ThunkTakePageChat} from "../../../../../redux/thunks/thunk_messages";
import {useDispatch, useSelector} from "react-redux";
import MessagesChat from "./MessagesChat/MessagesChat";
import MessagesChatDefault from "./MessagesChatDefault/MessagesChatDefault";

const MessagesChatBlock = () => {

  const location = useLocation();
  const dispatch = useDispatch();
  const {pageSize, pageCurrent, pageMax, items} =  useSelector(state => state.chats.currentChat);
  const urlId = parsingURL(location.search).sel;

  const CallBackTakeMessages = () => {
    pageMax >= pageCurrent && dispatch(ThunkTakePageChat(urlId, pageCurrent, pageSize))
  };

  useEffect(() => {
    dispatch(ThunkTakeFirstPageChat(urlId, 1, pageSize));
  }, [urlId]);

  return (
    <div className='MessagesChatBlock '>
      {urlId ? <MessagesChat items={items}
                             url={urlId}
                             CallBackTakeMessages={CallBackTakeMessages}/>
             : <MessagesChatDefault/>}
    </div>
  )
}

export default MessagesChatBlock;