import API, {API_MESSAGES} from "../../api/api";
import {
  addNewMessagesLocal,
  setItemsChat,
  setIsLoader,
  setIsLoaderSend,
  setDataChat, setInitialState, PageMax, setLoaderScrollMain, checkNewMessages, setCheckItems, deleteMassagesLocal
} from "../chats_reducer";
import {addNotificationInformation} from "../notifications_reducer/notifications_reducer";
import {setDialogs} from "../select_chat_reduser";


export const ThunkTakeDialogs = (urlId) => {
  return async (dispatch) => {
    const response = await API_MESSAGES.takeDialogs();
    dispatch(setDialogs(response.data))
    urlId !== undefined && response.data.forEach(el => el.hasNewMessages && el.id === Number(urlId)
      && dispatch(checkNewMessages(true)));
  };
};
export const ThunkTakeFirstPageChat = (id, page, count) => {
  return async (dispatch) => {
    dispatch(setIsLoader(true));
    dispatch(setInitialState())

    const response = await API_MESSAGES.takeChat(id, page, count);
    const resData = await  API.takeUserProfile(id);
    dispatch(setDataChat({
      name: resData.data.fullName,
      id: resData.data.userId,
      photo: resData.data.photos.small,
    }));
    dispatch(PageMax(response.data.totalCount))
    dispatch(setItemsChat(response.data.items));
    dispatch(setIsLoader(false));
  };
};
export const ThunkTakePageChat = (id, page, count) => {
  return async (dispatch) => {
    dispatch(setLoaderScrollMain(true));
    const response = await API_MESSAGES.takeChat(id, page, count);
    dispatch(setItemsChat(response.data.items));
    dispatch(setLoaderScrollMain(false));
  };
};
export const ThunkSendMessagesItem = (id , body, notification) => {
  return async (dispatch) => {
    dispatch(setIsLoaderSend(true));
    const response = await API_MESSAGES.SendMessagesItem(id, body);
    !notification && dispatch(ThunkTakeDialogs())
    notification && dispatch(addNotificationInformation(`Message sent!`));
    dispatch(addNewMessagesLocal([response.data.data.message]));
    dispatch(setIsLoaderSend(false));
  };
};
export const ThunkDeleteMessages = (id) => {
  return (dispatch) => {
    const response = API_MESSAGES.deleteMessages(id);
    dispatch(deleteMassagesLocal(id))
  };
};