let arrayNotificationsId = [];

export const notifications_id = () => {
  let id = Math.round(Math.random() * 1000);
  if(arrayNotificationsId.length === 0) {
    return id;
  } else {
    for (let i = 0; i < arrayNotificationsId.length; i++) {
      if(arrayNotificationsId[i] === id) {
        return notifications_id()
      } else {
        arrayNotificationsId.push(id)
        return id;
      }
    };
  }
}

