import {PageMax, setInitialState, SetUsers, ToggleLoader} from "../users_reducer";
import API from "../../api/api";

export const ThunkFirstTakeUsers = (pageSize, pageCurrent, term, friend) => {
  return async (dispatch) => {
    dispatch(ToggleLoader(true));
    dispatch(setInitialState())
    const response = await API.takeUsers(pageSize, pageCurrent, term, friend);
    dispatch(SetUsers(response.data.items));
    dispatch(PageMax(response.data.totalCount));
    dispatch(ToggleLoader(false));
  };
};
export const ThunkTakeUsers = (pageSize, pageCurrent, term, friend) => {
  return async (dispatch) => {
    dispatch(ToggleLoader(true));
    const response = await API.takeUsers(pageSize, pageCurrent, term, friend);
    dispatch(SetUsers(response.data.items));
    dispatch(ToggleLoader(false));
  };
};


