import './СhangeWidth.css';
import React from 'react';

const СhangeWidth = (props) => {
  const ChangeWidthBlockStyle = props => {
    switch (props.position) {
      case 'left':
        return {
          position: 'absolute',
          left: '0px',
          top: '50%',
          transform: 'translate(-50%, -50%)',
        };
      case 'right':
        return {
          position: 'absolute',
          right: '0px',
          top: '50%',
          transform: 'translate(-50%, -50%)',
        };
      case 'center':
        return {
          position: 'relative',
        };
    }
  };
  const ChangeWidthStyle = props => {
  switch (props.type) {
    case 'green':
      return 'ChangeWidthGreen'; //css
    case 'blue':
      return 'ChangeWidthBlue'; //css
  }
  };

  return (
    <div className='ChangeWidthBlock'
         style={ChangeWidthBlockStyle(props)}
         onMouseDown={props.Down}>
      <div className={'ChangeWidth ' + ChangeWidthStyle(props)}></div>
    </div>
  )
}

export default СhangeWidth;
