import './MessagesSelectMain.css';
import MessagesSelectItem from './MessagesSelectItem/MessagesSelectItem'
import {MessagesSelectMainHeader} from "./MessagesSelectMainHeader/MessagesSelectMainHeader";
import {MessagesSelectSearch} from "./MessagesSelectSearch/MessagesSelectSearch";
import {useSelector} from "react-redux";

const MessagesSelectMain = ({dialogs, dialogsPossible, dialogsSearch}) => {
  let isSearch = useSelector(state => state.select_chat.Search.isSearch)
  let isNotSearch = dialogsSearch.length === 0;

  return (
    <div className='MessagesSelectMain ScrollbarLittle'>
      {!isSearch && <MessagesSelectMainHeader dialogsPossible={dialogsPossible}/>}
      {!isSearch && dialogs.map((item) => <MessagesSelectItem itsState={item}/>)}
      {isSearch && dialogsSearch.map((item) => <MessagesSelectSearch itsState={item} isNotSearch={isNotSearch}/>)}
      {isSearch && isNotSearch && <div className='isNotSearch-block'>not found</div>}
    </div>
  )
}

export default MessagesSelectMain;