import './Notifications.css';
import NotificationsInformation from "./NotificationsInformation/NotificationsInformation";
import NotificationsErrors from "./NotificationsErrors/NotificationsErrors";
const INFORMATION = 'INFORMATION';
const ERROR = 'ERROR';

const Notifications = ({notifications, removeNotification, removeAllNotification}) => {

  const sortingNotifications = notifications.map(not => {
    switch (not.context) {
      case INFORMATION:
        return <NotificationsInformation key={not.id}
                                         data={not}
                                         removeNotification={removeNotification}
              />;
      case ERROR:
        return <NotificationsErrors key={not.id}
                                    data={not}
                                    removeNotification={removeNotification}
              />;
      default: return null;
    }
  });

  return (
    <div className='notifications-wrapper'>
      {notifications.length !== 0 && <div className='notifications-close-all' onClick={() => removeAllNotification()}>Close all</div>}
      {sortingNotifications}
    </div>
  )
};

export default Notifications;