import React, {useEffect} from 'react';
import {Formik, Form} from 'formik';
import {validateSingIn} from "../validate/validate";
import {useDispatch, useSelector} from "react-redux";
import {FormAuthItem} from "../FormAuthItem/FormAuthItem";
import {FormAuthItemPassword} from "../FormAuthItem/FormAuthItemPassword";
import {CheckboxAuth} from "../FormAuthItem/CheckboxAuth";
import {FormCaptcha} from "../FormAuthItem/FormCaptcha";
import {errorAuth} from "../validate/errorAuth";
import {setCaptcha, setWrongLoginAuth} from "../../../redux/myProfile_reducer";
import {ThunkAddLogin} from "../../../redux/thunks/thunk_auth";

const FormSingIn = () => {
  const dispatch = useDispatch();

  let wrongLoginAuth = useSelector((state) => state.myProfile.wrongLoginAuth);
  let loginAuthCaptcha = useSelector((state) => state.myProfile.loginAuthCaptcha);
  let buttonAuthDisable = useSelector((state) => state.myProfile.buttonAuthDisable);

  useEffect(() => {
    dispatch(setWrongLoginAuth(false));
    dispatch(setCaptcha(null));
  }, [])

  const submitSingIn = (values) => {
    dispatch(ThunkAddLogin(values.email, values.password, values.rememberMe, values.captcha));
    values.captcha = '';
  };

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
        rememberMe: false,
        captcha: '',
      }}
      onSubmit={submitSingIn}
      validate={validateSingIn}
    >
      {({
          errors,
          touched,
        }) => {

        errors = errorAuth(errors, wrongLoginAuth);

        return (
          <Form className='form-sign'>
            <FormAuthItem
              errors={errors.email}
              touched={touched.email}
              name='email'
              type='email'
              placeholder='email'
            />
            <FormAuthItemPassword
              errors={errors.password}
              touched={touched.password}
              name='password'
              type='password'
              placeholder='password'
            />
            <FormCaptcha
              name='captcha'
              placeholder='code'
              url={loginAuthCaptcha}
            />
            <CheckboxAuth
              name='rememberMe'
            />
            <button className='BtnGreen' type="submit" disabled={buttonAuthDisable}>Sing in</button>
          </Form>
        )}}
    </Formik>
  );
};

export default FormSingIn;

