import './MessagesChat.css'
import {MessagesChatHeader} from "./MessagesChatHeader/MessagesChatHeader";
import {MessagesChatMain} from "./MessagesChatMain/MessagesChatMain";
import {MessagesChatFooter} from "./MessagesChatFooter/MessagesChatFooter";
import {useSelector} from "react-redux";
import Preloader from "../../../../../../UniversalComponents/Preloader/Preloader";

const MessagesChat = ({items, url, CallBackTakeMessages}) => {

  const isLoader = useSelector(state => state.chats.isLoader);
  let dataChat = useSelector(state => state.chats.currentChat.data);

  if(isLoader) return <Preloader type='circle'/>
  return  (
      <div className='MessagesChat'>
        <MessagesChatHeader dataChat={dataChat}/>
        <MessagesChatMain items={items} CallBackTakeMessages={CallBackTakeMessages}/>
        <MessagesChatFooter url={url}/>
      </div>
  )
}

export default MessagesChat;