import './MessagesChatFooter.css';
import {useState} from "react";
import {ThunkSendMessagesItem} from "../../../../../../../redux/thunks/thunk_messages";
import {useDispatch, useSelector} from "react-redux";
import sendIcon from './../../../../../../../img/send.png'
import Preloader from "../../../../../../../UniversalComponents/Preloader/Preloader";

export const MessagesChatFooter = ({url}) => {
  const dispatch = useDispatch();
  let [value, setValue] = useState('');
  const isLoaderSend = useSelector(state => state.chats.isLoaderSend);

  const sendMessagesItem = () => {
    dispatch(ThunkSendMessagesItem(url,value, false))
    setValue('');
  };

  return (
    <form className='MessagesChatFooter' onSubmit={(e) => {
      e.preventDefault();
      !isLoaderSend && sendMessagesItem();
    }}>
      <input
        type='text'
        placeholder='Write a message...'
        className='MessagesChatFooter-textarea'
        value={value}
        onChange={(e) => setValue(e.currentTarget.value)}
      />
      {!isLoaderSend ? <div onClick={() => sendMessagesItem()} className='btn-send'><img src={sendIcon}/></div>
                      : <Preloader type='circle' className='btn-send'/>}
    </form>
  );
};