import {createSlice} from "@reduxjs/toolkit";

let initialState = {
  isLoader: true,
  isLoaderSend: false,
  scroll: null,
  isLoaderScrollMain: false,
  currentChat: {
    items: [],
    isNewMessages: false,
    data: {
      name: null,
      id: null,
      photo: null,
    },
    pageSize: 20, //max 20
    pageCurrent: 1,
    pageMax: 0,
  },
};

const chats_reducer = createSlice({
  name: 'chats_reducer',
  initialState,
  reducers: {
    deleteMassagesLocal (state, action) {
      state.currentChat.items = state.currentChat.items.filter(item => item.id !== action.payload)
    },
    setCheckItems (state) {
      state.currentChat.items = [...state.currentChat.items.map(el => el.viewed = true)];
    },
    checkNewMessages (state, action) {
      state.currentChat = {...state.currentChat, isNewMessages: action.payload};
    },
    setScroll (state, action) {
      state.scroll = action.payload;
    },
    setLoaderScrollMain (state, action) {
      state.isLoaderScrollMain = action.payload;
    },
    PageMax (state, action) {
      state.currentChat.pageMax = Math.ceil(action.payload / state.currentChat.pageSize);
    },
    setInitialState (state) {
      state.currentChat = {
        ...state.currentChat,
        items: [],
        data: {
        name: null,
          id: null,
          photo: null,
        },
        pageCurrent: 1,
        pageMax: 0,
      }
    },
    setIsLoaderSend (state, action) {
      state.isLoaderSend = action.payload;
    },
    addNewMessagesLocal (state, action) {
      state.currentChat = {...state.currentChat, items: [...state.currentChat.items, ...action.payload]};
    },
    setIsLoader (state, action) {
      state.isLoader = action.payload;
    },
    setItemsChat (state, action) {
      state.currentChat = {
        ...state.currentChat,
        items: [ ...action.payload, ...state.currentChat.items,],
        pageCurrent: state.currentChat.pageCurrent + 1,
      };
    },
    setDataChat (state, action) {
      state.currentChat.data = {
        ...state.currentChat.data,
        name: action.payload.name,
        id: action.payload.id,
        photo: action.payload.photo,
      };
    },
  }
});

export const {
  setCheckItems,
  deleteMassagesLocal,
  checkNewMessages,
  setScroll,
  setLoaderScrollMain,
  PageMax,
  setInitialState,
  setIsLoaderSend,
  addNewMessagesLocal,
  setIsLoader,
  setItemsChat,
  setDataChat
} = chats_reducer.actions;

export default chats_reducer.reducer;