import './MainHomeProfileContactsBlock.css';
import icon_vkontakteIcon from '../../../../../img/vkontakte.png';
import icon_twitter from '../../../../../img/twitter.png';
import icon_instagram from '../../../../../img/instagram.png';
import icon_github from '../../../../../img/github.png';
import icon_facebook from '../../../../../img/facebook.png';
import icon_youtube from '../../../../../img/youtube.png';
import GrayBoot from "../../../../../UniversalComponents/GrayBoot/GrayBoot";

const MainHomeProfileContactsBlock = ({contacts, isLoader}) => {
  let {vk, twitter, instagram, facebook, youtube, github} = contacts;
  let status = true;

  return status && (
    <div className='MainHomeProfileContactsBlock'>
      <div className='MainHomeProfileContactsHeader'>My contacts</div>
      {isLoader ? <GrayBoot sizeH={20}/> :
        <div className='MainHomeProfileContactsMain'>
        <MainHomeProfileContactsItem icon={icon_vkontakteIcon} link={vk}/>
        <MainHomeProfileContactsItem icon={icon_twitter} link={twitter}/>
        <MainHomeProfileContactsItem icon={icon_instagram} link={instagram}/>
        <MainHomeProfileContactsItem icon={icon_facebook} link={facebook}/>
        <MainHomeProfileContactsItem icon={icon_youtube} link={youtube}/>
        <MainHomeProfileContactsItem icon={icon_github} link={github}/>
        </div>}
    </div>
  );
};

const MainHomeProfileContactsItem = (props) => {
  return props.link != null &&
    <a className="MainHomeProfileContactsItem" href={props.link}>
      <img src={props.icon} />
    </a>
};

export default MainHomeProfileContactsBlock;