import {connect} from "react-redux";
import Notifications from "./Notifications/Notifications";
import {removeAllNotification, removeNotification} from "../../redux/notifications_reducer/notifications_reducer";

let mapStateToProps = state => {
  return {
    notifications: state.notifications.notifications,
  };
};

export default connect(mapStateToProps, {removeNotification, removeAllNotification})(Notifications);