import './Auth.css';
import {Navigate, NavLink} from "react-router-dom";
import {useEffect} from "react";
import FormSingUp from "./Form-sing-up/Form-sing-up";
import FormSingIn from "./Form-sing-in/Form-sing-in";
import {useDispatch, useSelector} from "react-redux";
import {ThunkSetMeProfile} from "../../redux/thunks/thunk_profile";

const Auth = ({context}) => {
  const dispatch = useDispatch();

  const isAuth = useSelector(state => state.myProfile.isAuth);
  const myId = useSelector(state => state.myProfile.myData.id);

  useEffect(() => {
    dispatch(ThunkSetMeProfile());
  }, [context]);

  return isAuth ? <Navigate to={`/${myId}`} /> : (
    <div className='login-wrapper'>
      <div className='form-wrapper'>
        <div className='form-name'>
          {context === 'login' ? 'Sign in' : 'Sign up'}
        </div>
        {context === 'login' ? <FormSingIn />
                             : <FormSingUp />}
        <p className='form-redirect-text'>
          {context === 'login' && "Don't have an account? - "}
          <NavLink to={context === 'login' ? '/registration' : '/login'}>
            {context !== 'login' ? 'Sign in' : 'Sign up'}
          </NavLink>
        </p>
      </div>

    </div>
  );
};

export default Auth;