import './MainHomeProfile.css';
import MainHomeProfileLookingForAJobBlock from "./MainHomeProfileLookingForAJobBlock/MainHomeProfileLookingForAJobBlock";
import MainHomeProfileAboutMeBlock from "./MainHomeProfileAboutMeBlock/MainHomeProfileAboutMeBlock";
import MainHomeProfileContactsBlock from "./MainHomeProfileContactsBlock/MainHomeProfileContactsBlock";
import MainHomeProfileHeaderFon from "./MainHomeProfileHeaderFon/MainHomeProfileHeaderFon";
import {useEffect} from "react";
import {useNavigate, useParams} from "react-router";
import {useDispatch, useSelector} from "react-redux";
import {ThunkTakeUserProfile} from "../../../../redux/thunks/thunk_profile";

const MainHomeProfile = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const profile = useSelector((state) => state.profile);
  const dataScroll = useSelector((state) => state.users.dataScroll);
  const my_id = useSelector((state) => state.myProfile.myData.id);
  let params = Number(useParams().id);

  useEffect(() => {
    dispatch(ThunkTakeUserProfile(params))
  }, [params]);

  const exitGO = () => {
    navigate(`/users?section=${dataScroll.section}`)
  }

  return (
    <div className='MainHomeProfile HomeRightBlock'>
      {my_id !== params && dataScroll.cache && <div className='btnExitProfile' onClick={() => exitGO()}><div className="arrow-7"><span></span><span></span><span></span></div></div>}
      <div className='MainHomeProfileHeader'>
        <MainHomeProfileHeaderFon isLoader={profile.isLoader}/>
        <MainHomeProfileAboutMeBlock profile={profile}
                                     id={params}
                                     my_id={my_id} />
      </div>
      <div className='MainHomeProfileMain'>
        <MainHomeProfileLookingForAJobBlock lookingForAJob={profile.dataProfile.lookingForAJob}
                                            lookingForAJobDescription={profile.dataProfile.lookingForAJobDescription}
                                            isLoader={profile.isLoader}/>
        <MainHomeProfileContactsBlock contacts={profile.dataProfile.contacts}
                                      isLoader={profile.isLoader}/>
      </div>
    </div>
  );
};

export default MainHomeProfile;


