import './MessagesSelectMainHeaderItem.css';
import nonePhoto from './../../../../../../../../img/NonePhoto.png';
import {NavLink} from "react-router-dom";

export const MessagesSelectMainHeaderItem = ({item}) => {
  return (
    <div className='MessagesSelectMainHeaderItem'>
      <NavLink to={`/messages?sel=${item.id}`}><img src={item.photos.small === null ? nonePhoto : item.photos.small}/></NavLink>
      <NavLink to={`/messages?sel=${item.id}`}><div className='MessagesSelectMainHeaderItem-name'>{item.name}</div></NavLink>
    </div>
  );
};