import API from "../../api/api";
import {changeStatusProfile, setUser, ToggleLoaderProfile} from "../profile_reducer";
import {setIsAuth, setMyData} from "../myProfile_reducer";
import {addNotificationErrors, addNotificationInformation} from "../notifications_reducer/notifications_reducer";

export const ThunkTakeUserProfile = (id) => {
  return async (dispatch) => {
    dispatch(ToggleLoaderProfile(true));

    const response_one = await API.takeUserProfile(id).catch(error =>
      dispatch(addNotificationErrors({content: error.response.data.message, update: false}))
    );
    const response_two = await API.takeStatus(id);

    dispatch(setUser(response_one.data));
    dispatch(changeStatusProfile(response_two.data))
    dispatch(ToggleLoaderProfile(false));
  };
};

export const ThunkSetMeProfile = () => {
  return async (dispatch) => {
    // dispatch(setIsAuth(null));
    const response = await API.meProfile();

    switch (response.data.resultCode) {
      case 0:
        dispatch(setMyData(response.data.data));
        dispatch(setIsAuth(true));
        break;
      case 1:
        dispatch(setIsAuth(false));
        break;
      default: break;
    };
  };
};

export const ThunkUpdateMyStatus = (status) => {
  return async (dispatch) => {
    const response = await API.updateStatus(status);

    switch (response.data.resultCode) {
      case 0:
        dispatch(addNotificationInformation(`Status was successfully changed to: ${status}`));
        dispatch(changeStatusProfile(status));
        break;
      case 1:
        break;
      default: break;
    };
  };
};
