import "./FollowButton.css";
import Preloader from "../../Preloader/Preloader";
import Confirmation from "../../Сonfirmation/Confirmation";
import {useEffect, useState} from "react";

const FollowButton = ({setLoaderBtn, ThunkFollowed, ThunkSetProfileFollowed, context, followed, id, isLoadBtn, name, myID}) => {
  let [isConfirmation, StatusConfirmation] = useState(false);

  useEffect(() => {
    if(context === 'profile') {
      id !== null ? ThunkSetProfileFollowed(id) : setLoaderBtn(id);
    }
  }, [id]);

  const CallBackFollowed = (type, id) => {
    ThunkFollowed(type, id, name);
    StatusConfirmation(false);
  };

  const BtnCheckdisabled = () => {
    let isLoadBtnItem = false;
    isLoadBtn.forEach(el => (el === id) ? isLoadBtnItem = true : null);
    return isLoadBtnItem;
  };

  if (myID === id) return null;
  return (
    <div className='FollowButton'>
      {isConfirmation && <Confirmation id={id}
                                       name={name}
                                       followed={followed}
                                       CallBackFollowed={CallBackFollowed}
                                       StatusConfirmation={StatusConfirmation}/>
      }
      <button onClick={() => StatusConfirmation(true)} disabled={BtnCheckdisabled()}>
        {BtnCheckdisabled() ? <Preloader type='circle' style={{height: 'min-content'}}/>
                            : followed ? '✓ Unfollow' : '♡ Follow'
        }
      </button>
    </div>
  );
};

export default FollowButton;



