import React, {useEffect, useState} from "react";
import ChangeWidth from "../../../../UniversalComponents/СhangeWidth/СhangeWidth";

const setTypeWidth = (width) => localStorage.setItem('width', width);
const getTypeWidth = () => localStorage.getItem('width');
const setTypeIcon = (icon) => localStorage.setItem('icon', icon);
const getTypeIcon = () => localStorage.getItem('icon');

const NavHomeWidthContainer = ({
                                 callBackSetWidth,
                                 callBackSetTypeIcon,
                                 currentWidth,
                                 CurrentTypeIcon
}) => {

  let [objDom, setObjDom] = useState(null);
  let [statusClick, setStatusClick] = useState(false);

  useEffect(() => {
    setStatusClick(true);
    setObjDom(document.querySelector('.NavHome'));
    callBackSetWidth(Number(getTypeWidth()) || 220);
    callBackSetTypeIcon(Number(getTypeIcon()) || 1);
  }, []);

  useEffect(() => {
    setTypeWidth(currentWidth);
    setTypeIcon(CurrentTypeIcon);
  }, [CurrentTypeIcon, currentWidth]);

  const Down = (e) => {
    objDom.style.transition = '0s';
    if (statusClick) {
      document.addEventListener('mousemove', Move);
      document.addEventListener('mouseup', Up);
      document.elementFromPoint(e.pageX, e.pageY);
    }
  };

  const Move = (e) => {
    if(statusClick) {
      let width = e.clientX - objDom.getBoundingClientRect().left;
      if(width >= 70 && width <= 220) callBackSetWidth(width);
      if(width >= 70 && width <= 200) callBackSetTypeIcon(2);
      if(width >= 200 && width <= 220) callBackSetTypeIcon(1);
    }
  };

  const Up = () => {
    let block = document.querySelector('.NavHome').style.minWidth;
    let actionBlock = block.substring(0,  block.length - 2);
    if(actionBlock >= 200) {
      callBackSetWidth(220);
      callBackSetTypeIcon(1);
    } else {
      callBackSetWidth(70);
      callBackSetTypeIcon(2);
    };
    objDom.style.transition = '0.2s cubic-bezier(0, 0, 0.3, 1)';
    document.removeEventListener('mouseup', Up);
    document.removeEventListener('mousemove', Move);
  };

  return <ChangeWidth Down={Down}
                      position='right'
                      type='blue'/>;
};

export default NavHomeWidthContainer;