import React, {useEffect, useState} from "react";
import ChangeWidth from "../../../../../UniversalComponents/СhangeWidth/СhangeWidth";

const setTypeWidth_message = (width) => localStorage.setItem('width_messages', width);
const getTypeWidth_message = () => localStorage.getItem('width_messages');

const MessagesChangeWidthContainer = ({
                                 callBackSetWidth,
}) => {
  let [objDom, setObjDom] = useState(null);
  let [statusClick, setStatusClick] = useState(false);

  useEffect(() => {
    setStatusClick(true);
    setObjDom(document.querySelector('.MessagesSelectBlock'));
    callBackSetWidth(Number(getTypeWidth_message()) || 300);
  }, []);

  const Down = (e) => {
    objDom.style.transition = '0s';
    if (statusClick) {
      document.addEventListener('mousemove', Move);
      document.addEventListener('mouseup', Up);
      document.elementFromPoint(e.pageX, e.pageY);
    }
  };

  const Move = (e) => {
    if(statusClick) {
      let width = e.clientX - objDom.getBoundingClientRect().left;
      if(width >= 300 && width <= 600) {
        setTypeWidth_message(width);
        callBackSetWidth(width);
      };
    }
  };

  const Up = () => {
    objDom.style.transition = '0.2s cubic-bezier(0, 0, 0.3, 1)';
    document.removeEventListener('mouseup', Up);
    document.removeEventListener('mousemove', Move);
  };

  return <ChangeWidth Down={Down}
                      position={'center'}
                      type='green'/>;
};

export default MessagesChangeWidthContainer;
