import './NotificationsErrors.css';
import {useState} from "react";

const NotificationsErrors = ({data, removeNotification}) => {
  let [isRemove, changeRemove] = useState(true);

  setTimeout(() => isRemove ? removeNotification(data.id) : null, 15000);

  const RemoveThisNotifications = () => {
    changeRemove(false);
    removeNotification(data.id);
  };

  return (
    <div className="notifications-errors">
      <div className='name-error'>ERROR</div>
      <p className='content-error'>{data.content}</p>
      {data.update && <button className='button-notifications-update' onClick={(e) => document.location.reload()}>Обновить</button>}
      <div className='cross-notifications-block' onClick={() => RemoveThisNotifications()}><span></span><span></span></div>
    </div>
  )
};

export default NotificationsErrors;


