import './Settings.css';
import {useDispatch, useSelector} from "react-redux";
import {ThunkRemoveLogin, ThunkTakeMyDataSettings, ThunkUpdatePhoto} from "../../../../redux/thunks/thunk_auth";
import {useEffect, useState} from "react";
import nonePhoto from './../../../../img/NonePhoto.png';
import Confirmation from "../../../../UniversalComponents/Сonfirmation/Confirmation";
import Preloader from "../../../../UniversalComponents/Preloader/Preloader";

const Settings = () => {

  const dispatch = useDispatch();
  const isLoaderUpdatePhoto = useSelector((state) => state.myProfile.isLoaderUpdatePhoto);
  const myData = useSelector((state) => state.myProfile.myData);
  let { email, login, id } = myData;

  useEffect(() => {
    dispatch(ThunkTakeMyDataSettings(id))
  }, []);

  const [isStatusConfirmation, setStatusConfirmation] = useState(false);
  const [dataFile, setDataFile] = useState(null);

  const checkPhoto = (e) => {
    setDataFile(e.currentTarget.files[0]);
    setStatusConfirmation(true);
  };

  const onUpdatePhoto = () => {
    dispatch(ThunkUpdatePhoto(dataFile))
    setStatusConfirmation(false);
  };

  const lol = () => {
    dispatch(ThunkRemoveLogin());
  };

  return (
    <div className='Settings HomeRightBlock'>
      {isStatusConfirmation &&
        <Confirmation
          name='To upload a photo'
          StatusConfirmation={setStatusConfirmation}
          CallBackFollowed={onUpdatePhoto}
      />
      }
      <div className='SettingsAllDataBlock'>
        <div className='SettingsAllDataBlockTwo'>
          <div className='SettingsAllDataPhoto'>
            <img src={myData.photo === null ? nonePhoto: myData.photo}/>
            {!isLoaderUpdatePhoto &&
              <label for="upload-photo" className='SettingsAllDataPhoto-download'>
              <div className='SettingsAllDataPhoto-download-cross mam1'></div>
              <div className='SettingsAllDataPhoto-download-cross mam2'></div>
              <p className='SettingsAllDataPhoto-text'>Upload a photo</p>
            </label>}
            {isLoaderUpdatePhoto && <Preloader type='circle' className='SettingsAllDataBlock-Preloader'/>}
            <input
              type='file'
              id='upload-photo'
              className='lol'
              onChange={checkPhoto}
            />
          </div>
          <div className='SettingsAllDataInfo'>
          <div className='SettingsAllDataInfo-block-item'>
            <span>Name:</span>
            <div className='SettingsAllData'>{login}</div>
          </div>
          <div className='SettingsAllDataInfo-block-item'>
            <span>Email:</span>
            <div className='SettingsAllData'>{email}</div>
          </div>
          <div className='SettingsAllDataInfo-block-item'>
            <span>id:</span>
            <div className='SettingsAllData'>{id}</div>
          </div>
        </div>
        </div>
        <button
          onClick={() => lol()}
          className='Settings-btn BtnGreen'
        >Sign ou</button>
      </div>
    </div>
  )
}

export default Settings;