import React, {useEffect} from "react";

export const UserRadioItem = ({name, type, context, setContext, setCoordinates}) => {
  const radioItem = React.createRef();

  useEffect(() => {
    context === type && setCoordinates(radioItem.current.getBoundingClientRect())
  });

  const callBack = (e) => {
    setContext(type);
    setCoordinates(e.currentTarget.getBoundingClientRect());
  };

  return (
    <div
      ref={radioItem}
      onClick={(e) => callBack(e)}
      className={`${context === type && 'header-radio-item-active'} header-radio-item`}
    >
      {name}
    </div>
  );
};