import './NavHomePoint.css';
import {NavLink} from "react-router-dom";

const NavHomePoint = (props) => {
  const TypeIcon = (props) => {
    switch (props.typeIcon) {
      case 1:
        return (
          <div className='NavHomePoint'>
            <p>{props.title}</p>
          </div>
        );
      case 2:
        return  (
          <div className='navHomeTypeIcon_2_BLOCK'>
            <img src={props.Icon_1}/>
          </div>
        );
    }
  }

  return (
    <NavLink to={props.to}>
      {TypeIcon(props)}
    </NavLink>
  )
}

export default NavHomePoint;