import './MessagesSelectSearch.css';
import {NavLink} from "react-router-dom";
import nonePhoto from '../../../../../../../img/NonePhoto.png';

export const MessagesSelectSearch = ({itsState}) => {
  return (
    <NavLink to={`?sel=${itsState.id}`} className='MessagesSelectSearch'>
      <img
        className='MessagesSelectSearch-photo'
        src={itsState.photos.small === null ? nonePhoto : itsState.photos.small}
      />
      <div className='MessagesSelectSearch-name'>{itsState.name === undefined ? itsState.userName : itsState.name}</div>
    </NavLink>
  );
};