import {Field} from "formik";

export const FormCaptcha = ({name, placeholder, url}) => {
  return url !== null && (
    <div className='captcha-block label'>
      <div className='captcha-img'>
        <img src={url}/>
      </div>
      <Field
        type='text'
        maxLength='6'
        name={name}
        className='captcha-input'
        placeholder={placeholder}
      />
    </div>
  );
};