import API from "../../api/api";
import {ThunkSetMeProfile} from "./thunk_profile";
import {
  setButtonAuthDisable,
  setCaptcha,
  setDataProfileSettings,
  setIsLoaderUpdatePhoto,
  setWrongLoginAuth
} from "../myProfile_reducer";
import {addNotificationInformation} from "../notifications_reducer/notifications_reducer";

export const ThunkAddLogin = (email, password, rememberMe, captcha) => {
  return async (dispatch) => {
    dispatch(setButtonAuthDisable(true));

    const response = await API.addAuth(email, password, rememberMe, captcha);

    let text = response.data.messages[0];

    switch (response.data.resultCode) {
      case 0:
        dispatch(ThunkSetMeProfile());
        dispatch(setButtonAuthDisable(false));
        dispatch(setCaptcha(null));
        dispatch(setWrongLoginAuth(null));
        break;
      case 1:
        dispatch(setWrongLoginAuth(text));
        dispatch(setButtonAuthDisable(false));
        dispatch(setCaptcha(null));
        break;
      case 10:
        const response = await API.takeCaptcha()
        dispatch(setWrongLoginAuth(text));
        dispatch(setCaptcha(response.data.url));
        dispatch(setButtonAuthDisable(false));
        break;
      default: break;
    };
  };
};

export const ThunkRemoveLogin = () => {
  return async (dispatch) => {
    const response = await API.removeAuth();
    switch (response.data.resultCode) {
      case 0:
        dispatch(ThunkSetMeProfile())
        break;
      default: break;
    };
  };
};

export const ThunkTakeMyDataSettings = (id) => {
  return async (dispatch) => {
    const response = await API.takeUserProfile(id);
    dispatch(setDataProfileSettings({photo: response.data.photos.large}))
  };
};

export const ThunkUpdatePhoto = (file) => {
  return async (dispatch) => {
    dispatch(setIsLoaderUpdatePhoto(true));
    const response = await API.updatePhoto(file);
    dispatch(setDataProfileSettings({photo: response.data.data.photos.large}))
    dispatch(addNotificationInformation(`Photo uploaded successfully`));
    dispatch(setIsLoaderUpdatePhoto(false));
  };
};