import React, {useState} from "react";
import SaveIcon from "../../../../../../../img/save.png";

const StatusMeEdit = ({aboutMe, ToggleEdit, callBackChangeValue}) => {
  const VALUE_EDIT = React.createRef();
  let [valueEdit, changeValueEdit] = useState(String(aboutMe));

  return (
    <form className='MainHomeProfileAboutMeStatus' onSubmit={(event) => event.preventDefault()}>
      <input  className='StatusInput'
              type='text'
              autoFocus={true}
              ref={VALUE_EDIT}
              onBlur={() => ToggleEdit(false)}
              value={valueEdit}
              onChange={() => changeValueEdit(VALUE_EDIT.current.value)}
      />
      <button onClick={() => callBackChangeValue(valueEdit)}><img src={SaveIcon}/></button>
    </form>
  );
};

export default StatusMeEdit;