import {createSlice} from "@reduxjs/toolkit";

let initialState = {
  isLoaderUpdatePhoto: false,
  myData: {
    email: null,
    login: null,
    id: null,
    photo: null,
  },
  isAuth: null,
  wrongLoginAuth: false,
  loginAuthCaptcha: null,
  buttonAuthDisable: false,
};

const myProfile_reducer = createSlice({
  name: 'myProfile_reducer',
  initialState,
  reducers: {
    setIsLoaderUpdatePhoto (state, action) {
      state.isLoaderUpdatePhoto = action.payload;
    },
    setDataProfileSettings (state, action) {
      state.myData = {
        ...state.myData,
        photo: action.payload.photo,
      };
    },
    setButtonAuthDisable (state, action) {
      state.buttonAuthDisable = action.payload;
    },
    setCaptcha (state, action) {
      state.loginAuthCaptcha = action.payload;
    },
    setWrongLoginAuth (state, action) {
      state.wrongLoginAuth = action.payload;
    },
    setIsAuth (state, action) {
      state.isAuth = action.payload;
    },
    setMyData (state, action) {
      state.myData = {
        ...state.myData,
        email: action.payload.email,
        login: action.payload.login,
        id: action.payload.id,
      };
    },
  },
});

export const {
  setButtonAuthDisable,
  setCaptcha,
  setWrongLoginAuth,
  setIsAuth,
  setMyData,
  setDataProfileSettings,
  setIsLoaderUpdatePhoto
} = myProfile_reducer.actions;

export default myProfile_reducer.reducer;








