import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import './MessagesSelectMainHeader.css';
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Pagination, Navigation } from "swiper";
import {MessagesSelectMainHeaderItem} from "./MessagesSelectMainHeaderItem/MessagesSelectMainHeaderItem";

export const MessagesSelectMainHeader = ({dialogsPossible}) => {

  // useEffect(() => {
  //   dispatch(ThunkTakeFirstSelectMessages(pageSize, 1, '', true))
  // }, []);


  return  (
    <>
    <Swiper
      slidesPerView='auto'
      spaceBetween={15}
      slidesPerGroup={3}
      resistanceRatio={0}
      loopFillGroupWithBlank={true}
      navigation={true}
      modules={[Pagination, Navigation]}
      className='MessagesSelectMain_header-block'
    >
      {dialogsPossible.map( item => {
        return (
          <SwiperSlide>
            <MessagesSelectMainHeaderItem item={item}/>
          </SwiperSlide>
        )})
      }
    </Swiper>
    <div className='long-MessagesSelectMainHeaderItem'></div>
    </>
  );
};