import {ErrorMessage, Field} from "formik";
import {useState} from "react";
import {ErrorBlock} from "./ErrorBlock";

export const FormAuthItemPassword = ({name, type, errors, touched, placeholder}) => {
  let [typePassword, setTypePassword] = useState(type)

  const toggleTypePassword = () => typePassword === 'password' ? setTypePassword('text')
    : setTypePassword('password');

  let stylePasswordLong = {
    display: typePassword === 'password' ? 'block' : 'none',
  }

  return (
    <div className='label'>
      <Field
        type={typePassword}
        name={name}
        placeholder={placeholder}
        className={`form-sign-input ${(errors !== undefined && touched) && 'inputRedBorder'}`}
      />
      <div onClick={() => toggleTypePassword()} className='eye-password'>
        <span id="eye"></span>
        <div className='eye-password-long' style={stylePasswordLong}></div>
      </div>
      <ErrorBlock errors={errors} touched={touched}/>
    </div>
  );
};

