import './NotificationsInformation.css';
import { useState } from "react";

const NotificationsInformation = ({data, removeNotification}) => {
  let [isRemove, changeRemove] = useState(true);

  setTimeout(() => isRemove ? removeNotification(data.id) : null, 7000);

  const RemoveThisNotifications = () => {
    changeRemove(false);
    removeNotification(data.id);
  };

  return (
    <div className='notifications-information'>
      <div className='name-information'>NOTIFICATION</div>
      <p className='content-information'>{data.content}</p>
      <div className='cross-notifications-block' onClick={() => RemoveThisNotifications()}><span></span><span></span></div>
    </div>
  )
};

export default NotificationsInformation;