import './WriteMessage.css';
import {useState} from "react";
import {ThunkSendMessagesItem} from "../../../../../../redux/thunks/thunk_messages";
import {useDispatch} from "react-redux";
import NullAvatar from '../../../../../../img/NonePhoto.png';

export const WriteMessage = ({name, id, photo}) => {
  let [isShowBlock, setIsShowBlock] = useState(false);
  let [value, setValue] = useState('');
  const dispatch = useDispatch();

  const callBackSend = () => {
    dispatch(ThunkSendMessagesItem(id, value, true));
    setIsShowBlock(false);
    setValue('');
  }

  return (
    <>
      {isShowBlock &&
        <div className='WriteMessage-alert-block'>
        <div className='WriteMessage-alert'>
          <div className='WriteMessage-alert-header'>
            <p>A new message</p>
          </div>
          <div className='WriteMessage-alert-main'>
            <div className='WriteMessage-alert-main-data'>
              <img src={photo === null ? NullAvatar : photo}/>
              <p>{name}</p>
            </div>
            <div className='WriteMessage-alert-main-form'>
              <input
                placeholder='Write a message...'
                type='text'
                autoFocus
                value={value}
                onChange={e => setValue(e.currentTarget.value)}
                className='WriteMessage-alert-main-input'/>
              <div className='WriteMessage-alert-main-form-btns'>
                <button onClick={() => setIsShowBlock(false)}>Cancel</button>
                <button className='BtnGreen' onClick={() => callBackSend()}>Continue</button>
              </div>
            </div>
          </div>
        </div>
      </div>}
      <button className='WriteMessage-btn' onClick={() => setIsShowBlock(true)}>send a message</button>
    </>
  );
};