import './MessagesChatMain.css';
import React, {useEffect, useState} from 'react'
import {useDispatch, useSelector} from "react-redux";
import {RenderItemMessagesMy} from "./renderItemMessagesTyps/renderItemMessagesMy";
import {RenderItemMessagesСompanion,} from "./renderItemMessagesTyps/renderItemMessagesСompanion";
import Preloader from "../../../../../../../UniversalComponents/Preloader/Preloader";
import {setScroll} from "../../../../../../../redux/chats_reducer";
import Confirmation from "../../../../../../../UniversalComponents/Сonfirmation/Confirmation";
import {ThunkDeleteMessages} from "../../../../../../../redux/thunks/thunk_messages";

export const MessagesChatMain = ({items, CallBackTakeMessages}) => {
  const dispatch = useDispatch();
  const isLoaderScrollMain = useSelector(state => state.chats.isLoaderScrollMain);
  const scroll = useSelector(state => state.chats.scroll)
  const myId = useSelector(state => state.myProfile.myData.id);
  const SCROLL_CHAT = React.createRef();
  const SCROLL_HEADTH = React.createRef();

  const CallBackScroll = () => {
    let blockScroll =  document.querySelector('.MessagesChatMain-wrapper');
    let maxScrollHeight = Math.max(blockScroll.scrollHeight - SCROLL_CHAT.current.getBoundingClientRect().height);

    dispatch(setScroll(maxScrollHeight - SCROLL_CHAT.current.scrollTop));
    SCROLL_CHAT.current.scrollTop === 0 && CallBackTakeMessages()
  };
  useEffect(() => {
    let blockScroll =  document.querySelector('.MessagesChatMain-wrapper');
    let maxScrollHeight = Math.max(blockScroll.scrollHeight - SCROLL_CHAT.current.getBoundingClientRect().height);
    SCROLL_CHAT.current.scrollTop = maxScrollHeight - scroll
  }, [CallBackTakeMessages]);
  useEffect(() => {
    SCROLL_CHAT.current.scrollTop = SCROLL_HEADTH.current.getBoundingClientRect().height;
  }, []);

  const [isDeleteBlock, setIsDeleteBlock] = useState(false);
  const [idDelete, setIdDelete] = useState(null);

  const checkDeleteMassages = (id) => {
    setIsDeleteBlock(true);
    setIdDelete(id);
  };

  const onDeleteMassages = () => {
    dispatch(ThunkDeleteMessages(idDelete));
    setIsDeleteBlock(false);
  };

  return (
    <div className='MessagesChatMain-wrapper'
         ref={SCROLL_CHAT}
         onScroll={() => CallBackScroll()}
    >
      {isLoaderScrollMain &&
        <Preloader
          type='circle'
          context='absolute'
          className='MessagesChatMain-wrapper-circle'
        />
      }
      <div className='MessagesChatMain' ref={SCROLL_HEADTH}>
      { items && items.map(item =>
        item.senderId === myId
          ? <RenderItemMessagesMy
              onDeleteMassages={checkDeleteMassages}
              item={item}
            />
          : <RenderItemMessagesСompanion
              onDeleteMassages={checkDeleteMassages}
              item={item}
            />
      )}
      </div>
      {isDeleteBlock &&
        <Confirmation
          name='Delete message'
          CallBackFollowed={onDeleteMassages}
          StatusConfirmation={setIsDeleteBlock}
        />
      }
    </div>
  );
};