const checkSearch = (word, value) => {
  if( value === '' || value === null) return true
  let valueAll = value.toUpperCase()
  word = word.toUpperCase()
  let status;

  for (let i = 0; i < valueAll.length; i++) {
    let letter = valueAll[i];
    if (letter === word[i]) status = true
    else { status = false; break }
  };
  return status;
};

export default checkSearch;
