import './NavHome.css';
import {NavLink} from "react-router-dom";
import NavHomeWidthConteiner from './NavHomeWidthConteiner/NavHomeWidthConteiner';
import NavHomePoint from "./NavHomePoint/NavHomePoint";
import ProfileNavIcon from '../../../img/profileNav.png';
import MessagesNavIcon from '../../../img/messagesNav.png';
import FollowingsIcon from '../../../img/FollowingsNav.png';
import UsersNavIcon from '../../../img/usersNav.png';
import titleIcon from '../../../img/titleIcon.png'
import {useSelector} from "react-redux";
import {useState} from "react";

const NavHome = () => {
  let [currentWidth, setCurrentWidth] = useState(null);
  let [CurrentTypeIcon, setCurrentTypeIcon] = useState(null);
  const myProfile = useSelector(state => state.myProfile);

  const callBackSetWidth = (width) => {
    setCurrentWidth(width);
  };
  const callBackSetTypeIcon = (width) => {
    setCurrentTypeIcon(width);
  };

  const NavHomeHeader = (type) => {
    switch (type) {
      case 1:
        return (
          <div className='NavHomeHeader'>NETWORK</div>
        );
      case 2:
        return (
          <div className='navHomeTypeIcon_2_BLOCK NavHomeHeader_2'>
            <img src={titleIcon}/>
          </div>
        );
    }
  };
  const BtnSettings = (type) => {
    switch (type) {
      case 1:
        return (
          <>
            <div className="loadingio-spinner-gear-yjzm6iuvmmn"><div className="ldio-6sgfe60wrpc"><div><div></div><div></div><div></div><div></div><div></div><div></div></div></div><div className='ldio-6sgfe60wrpcwwge'></div></div>
            <span>settings</span>
          </>
        );
      case 2:
        return (
          <div className="loadingio-spinner-gear-yjzm6iuvmmn"><div className="ldio-6sgfe60wrpc"><div><div></div><div></div><div></div><div></div><div></div><div></div></div></div><div className='ldio-6sgfe60wrpcwwge'></div></div>
        );
    }
  };
  const MLeftBtn = () => {
    if (CurrentTypeIcon === 1) {
      return '30px'
    } else
    if (CurrentTypeIcon === 2) {
      return 'auto'
    }
  };
  const NavHomePointBottomStyle = {
    marginLeft: MLeftBtn(),
  };

  return (
    <div className='NavHome' style={{minWidth: currentWidth + 'px'}}>
      <div className='NavHomeHeaderBlock'>
        <NavLink to='news'>
          {NavHomeHeader(CurrentTypeIcon)}
        </NavLink>
      </div>
      <div className='NavHomePointsBlock'>
        <NavHomePoint to={'/' + myProfile.myData.id}
                      title='Profile'
                      typeIcon={CurrentTypeIcon}
                      Icon_1={ProfileNavIcon}/>
        <NavHomePoint to='messages'
                      title='Messages'
                      typeIcon={CurrentTypeIcon}
                      Icon_1={MessagesNavIcon}/>
        <NavHomePoint to='users'
                      title='Users'
                      typeIcon={CurrentTypeIcon}
                      Icon_1={UsersNavIcon}/>

        <div className='NavHomePointBottom' style={NavHomePointBottomStyle}>
          <NavLink to='/settings'>
            <button className='btnSettings' >
              {BtnSettings(CurrentTypeIcon)}
            </button>
          </NavLink>
        </div>
      </div>
      <NavHomeWidthConteiner
        callBackSetWidth={callBackSetWidth}
        callBackSetTypeIcon={callBackSetTypeIcon}
        CurrentTypeIcon={CurrentTypeIcon}
        currentWidth={currentWidth}
      />
    </div>
  )
}
export default NavHome;