import instance from "./instance";

export const API_MESSAGES = {
  deleteMessages (id) {
    return instance.delete(`dialogs/messages/${id}`);
  },
  takeNewMessages (urlId, endTimeMessage) {
    return instance.get(`dialogs/${urlId}/messages/new?newerThen=${endTimeMessage}`);
  },
  takeDialogs () {
    return instance.get(`dialogs`);
  },
  takeChat (id, page, count) {
    return instance.get(`dialogs/${id}/messages?count=${count}&page=${page}`);
  },
  SendMessagesItem (id, body) {
    return instance.post(`dialogs/${id}/messages`, {body});
  },
};

let API = {
  updatePhoto (file) {
    const formData = new FormData();
    formData.append('image' , file)
    return instance.put(`/profile/photo`, formData,{
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  takeUsers (pageSize, pageCurrent , term, friend) {
    return instance.get(`users?count=${pageSize}&page=${pageCurrent}&term=${term}&friend=${friend}`);
  },
  followed (id) {
    return instance.get(`follow/${id}`);
  },
  follow (id) {
    return instance.post(`follow/${id}`);
  },
  unfollow (id) {
    return instance.delete(`follow/${id}`);
  },
  takeUserProfile(id) {
    return instance.get(`profile/${id}`);
  },
  meProfile () {
    return instance.get(`auth/me`);
  },
  updateStatus (status) {
    return instance.put(`profile/status`, {status});
  },
  takeStatus (id) {
    return instance.get(`profile/status/${id}`);
  },
  addAuth (email, password, rememberMe, captcha) {
    return instance.post(`auth/login`, {email, password, rememberMe, captcha});
  },
  removeAuth () {
    return instance.delete(`auth/login`);
  },
  takeCaptcha () {
    return instance.get(`security/get-captcha-url`)
  },
};

export default API;
