import './Confirmation.css';

const Confirmation = ({id, name, followed, StatusConfirmation, CallBackFollowed}) => {
  return (
    <div className='confirmation-wrapper' >
      <div className='confirmation'>
        <div className='confirmationHeader'>CONFIRM</div>
        <div className='confirmationMain'>
          {followed !== undefined && <p>{`${followed ? 'Unfollow' : 'Follow'} ${name}?`}</p>}
          {followed === undefined && <p>{`${name}? `}</p>}
          <div className='confirmatioBtnBlock'>
            <button onClick={() => StatusConfirmation(false)}>Сancel</button>
            <button className='BtnGreen'
                    onClick={() => CallBackFollowed(followed ? 'UNFOLLOW' : 'FOLLOW', id)}>Сontinue</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Confirmation;