import {createSlice} from "@reduxjs/toolkit";

let initialState = {
  usersAll: [],
  term: '',
  pageSize: 50, //max 100
  pageCurrent: 1,
  pageMax: 0,
  isLoader: true,
  totalCount: null,
  isLoadBtn: [],
  dataScroll: {
    cache: false,
    scroll: null,
    section: null,
  },
};

const users_reducer = createSlice({
  name: 'users_reducer',
  initialState,
  reducers: {
    setScroll (state, action) {
      state.dataScroll = {
        cache: action.payload.cache,
        scroll: action.payload.scrollTop,
        section: action.payload.section,
      };
    },
    changeStatusFollowed (state, action) {
      state.usersAll = state.usersAll.map(item =>
        item.id === action.payload.id ? {...item, followed: action.payload.followed} : item);
    },
    setTerm (state, action) {
      state.term = action.payload;
    },
    setInitialState (state) {
      state.usersAll = [];
      state.pageCurrent = 1;
      state.pageMax = 0;
      state.totalCount = null;
      state.isLoader = true;
      state.firstRequest = false;
    },
    setLoaderBtn (state, action) {
      state.isLoadBtn = [...state.isLoadBtn, action.payload];
    },
    removeLoaderBtn (state, action) {
      state.isLoadBtn = [...state.isLoadBtn.filter(el => action.payload !== el)];
    },
    ToggleLoader (state, action) {
      state.isLoader = action.payload;
    },
    PageMax (state, action) {
      state.pageMax = Math.ceil(action.payload / state.pageSize);
      state.totalCount = action.payload;
    },
    SetUsers (state, action) {
      state.usersAll = [...state.usersAll, ...action.payload];
      state.pageCurrent = state.pageCurrent + 1;
    },
  },
});

export const {
  setScroll,
  changeStatusFollowed,
  setTerm,
  setInitialState,
  setLoaderBtn,
  removeLoaderBtn,
  ToggleLoader,
  PageMax,
  SetUsers
} = users_reducer.actions;

export default users_reducer.reducer;
