import {changeStatusFollowed, removeLoaderBtn, setLoaderBtn} from "../users_reducer";
import {changeStatusFollowedProfile} from "../profile_reducer";
import API from "../../api/api";
import {addNotificationInformation} from "../notifications_reducer/notifications_reducer";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';

export const ThunkFollowed  = (type, id, name) => {
  return async (dispatch) => {
    dispatch(setLoaderBtn(id));

    const changeLocalFollowedAll = (id, boolean) => {
      dispatch(changeStatusFollowedProfile(boolean));
      dispatch(changeStatusFollowed({id, followed: boolean}));
      dispatch(removeLoaderBtn(id));
    };

    const response = type === FOLLOW ? await API.follow(id) : await API.unfollow(id);

    if(response.data.resultCode === 0) {
      if(type === FOLLOW) {
        changeLocalFollowedAll(id, true);
        dispatch(addNotificationInformation(`Follow to ${name} was successful!`));
      } else
      if(type === UNFOLLOW) {
        changeLocalFollowedAll(id, false);
        dispatch(addNotificationInformation(`Unfollow to ${name} was successful!`));
      };
    };
  };
};

export const ThunkSetProfileFollowed = (id) => {
  return async (dispatch) => {
    // dispatch(setLoaderBtn(id));
    const response = await API.followed(id);

    dispatch(changeStatusFollowedProfile(response.data));
    dispatch(removeLoaderBtn(id));
  };
};