import './MainHomeMessages.css';
import MessagesChatBlock from "./MessagesChatBlock/MessagesChatBlock";
import MessagesChangeWidthContainer from "./MessagesСhangeWidthConteiner/MessagesСhangeWidthConteiner";
import MessagesSelectBlock from "./MessagesSelectBlock/MessagesSelectBlock";
import {useState} from "react";

const MainHomeMessages = () => {

  let [currentWidth, setCurrentWidth] = useState(null);

  const callBackSetWidth = (width) => {
    setCurrentWidth(width);
  };

  return (
    <div className='MainHomeMessages HomeRightBlock' >
      <MessagesSelectBlock currentWidth={currentWidth}/>
      <MessagesChangeWidthContainer
        callBackSetWidth={callBackSetWidth}
      />
      <MessagesChatBlock />
    </div>
  )
}

export default MainHomeMessages;