import React, {useState} from "react";
import GrayBoot from "../../../../../../../UniversalComponents/GrayBoot/GrayBoot";
import EditIcon from "../../../../../../../img/edit.png";

const StatusMe = ({isLoader, aboutMe, ToggleEdit}) => {
  let [DisplayEdit, ToggleDisplayEdit] = useState(false);

  return (
    <div className='MainHomeProfileAboutMeStatus'
         onClick={() => ToggleEdit(true)}
         onMouseOver={() => ToggleDisplayEdit(true)}
         onMouseOut={() => ToggleDisplayEdit(false)}>
      {isLoader ? <GrayBoot sizeW={40} sizeH={15}/>
                : aboutMe === '' || aboutMe === null ? <span className='addStatus'>добавить стутус</span> : aboutMe}
      <div className='MainHomeProfileAboutMeStatusEditBlock'>
        {DisplayEdit && <img src={EditIcon}/>}
      </div>
    </div>
  );
};

export default StatusMe;