import React from 'react';
import {connect} from "react-redux";
import { Navigate } from "react-router-dom";
import {ThunkSetMeProfile} from "../../redux/thunks/thunk_profile";

const AuthHoc = (Component) => {

  class AuthHocContainer extends React.Component  {
      constructor(props) {
      super(props)
    }
    componentDidMount() {
      this.props.ThunkSetMeProfile();
    }

    render () {
      if(this.props.isAuth === null) return null;
      return this.props.isAuth ? <Component /> : <Navigate to="/login" />
      };
  };

  let mapStateToProps = state => {
    return {
      isAuth: state.myProfile.isAuth,
    };
  };

  return connect(mapStateToProps, {ThunkSetMeProfile})(AuthHocContainer);
};

export default AuthHoc;