import React  from 'react';
import {useState} from "react";
import StatusMe from "./StatusMe/StatusMe";
import StatusMeEdit from "./StatusMeEdit/StatusMeEdit";
import StatusPeople from "./StatusPeople/StatusPeople";
import {useDispatch} from "react-redux";
import {ThunkUpdateMyStatus} from "../../../../../../redux/thunks/thunk_profile";

const StatusProfile = ({isMy, isLoader, aboutMe}) => {
  let [isEdit, ToggleEdit] = useState(false);
  const dispatch = useDispatch();

  const callBackChangeValue = (status) => {
    dispatch(ThunkUpdateMyStatus(status));
    ToggleEdit(false);
  };

  return (
    <>{
        isMy ? isEdit ? <StatusMeEdit aboutMe={aboutMe}
                                      ToggleEdit={ToggleEdit}
                                      callBackChangeValue={callBackChangeValue}/>
                      : <StatusMe isLoader={isLoader}
                                  aboutMe={aboutMe}
                                  ToggleEdit={ToggleEdit}/>
             : <StatusPeople isLoader={isLoader} aboutMe={aboutMe}/>
    }</>
  );
};

export default StatusProfile;


