import {notifications_id} from "./notifications_id";
import {createSlice} from "@reduxjs/toolkit";

const REMOVE_NOTIFICATION = 'REMOVE_NOTIFICATION';
const ADD_NOTIFICATION_INFORMATION = 'ADD_NOTIFICATION_INFORMATION';
const ADD_NOTIFICATION_ERROR = 'ADD_NOTIFICATION_ERROR';
const INFORMATION = 'INFORMATION';
const ERROR = 'ERROR';
const REMOVE_ALL_NOTIFICATION = 'REMOVE_ALL_NOTIFICATION';


let initialState = {
  notifications: [],
};


const notifications = createSlice({
  name: 'notifications',
  initialState,
  reducers: {
    addNotificationErrors (state, action) {
      state.notifications = [
        ...state.notifications,
      {
        context: ERROR,
        content: action.payload.content,
        update: action.payload.update,
      }]
    },
    addNotificationInformation (state, action) {
      state.notifications = [
        ...state.notifications,
        {
          context: INFORMATION,
          content: action.payload,
          id: notifications_id()
        }]
    },
    removeAllNotification (state) {
      state.notifications = [];
    },
    removeNotification (state, action) {
      state.notifications = [...state.notifications.filter(el => el.id !== action.payload)]
    },
  },
});

