import {useState} from "react";

export const RenderItemMessagesСompanion = ({item, onDeleteMassages}) => {
  let [back, setBack] = useState('transparent');
  let [Design, setDesign] = useState('#0D1117FF');
  let [transition, setTransition] = useState('0s');

  let styleWrapper = {
    background: back,
    transition: transition
  };

  let styleDesign = {
    background: Design,
    transition: transition
  };

  const MouseOver = () => {
    setTransition('0s');
    setBack('#131922');
    setDesign('#131922')
  };
  const MouseOut = () => {
    setTransition('0.3s');
    setBack('transparent');
    setDesign('#0D1117FF')
  };

  const time = new Date(item?.addedAt);

  return (
    <div className='renderItemMessages-wrapper'
         onMouseOver={() => MouseOver()}
         onMouseOut={() => MouseOut()}
         style={styleWrapper}
         onClick={() => onDeleteMassages(item.id)}
    >
      <div className='renderItemMessages-block itemMesСompanion'>
        <p>{item.body}</p>
        <span>{time ? time.getHours() + ":" + time.getMinutes() : ''}</span>
        <div className='itemMesСompanion_design_01'></div>
        <div className='itemMesСompanion_design_02' style={styleDesign}></div>
      </div>
    </div>
  );
};