import './MainHomeUsersItem.css';
import {NavLink} from "react-router-dom";
import NullAvatar from '../../../../../img/NonePhoto.png';
import FOTO_BACK from '../../../../../img/1-1.png';
import React from "react";
import FollowButtonConteiner from "../../../../../UniversalComponents/FollowButtonConteiner/FollowButtonConteiner";

const MainHomeUsersItem = ({itemState, saveScroll}) => {
  return (
    <div className="MainHomeUsersItem">
      <NavLink to={`/${itemState.id}`} onClick={() => saveScroll()}>
       <div className="MainHomeUsersItemPhotoBlock">
         <img src={FOTO_BACK} className='MainHomeUsersItemPhotoBack'/>
          <img className='MainHomeUsersItemPhotoMain'
               src={itemState.photos.large === null ? NullAvatar : itemState.photos.large} />
       </div>
      </NavLink>
      <NavLink to={`/${itemState.id}`} onClick={() => saveScroll()}>
        <p className="MainHomeUsersItemName">{itemState.name}</p>
      </NavLink>
      <div className="MainHomeUsersItemBtn">
        <FollowButtonConteiner id={itemState.id}
                               context='users'
                               followed={itemState.followed}
                               name={itemState.name}/>
      </div>
    </div>
  );
};

export default MainHomeUsersItem;