import React, { useState} from "react";
import {UserRadioItem} from "./UserRadioItem";

export const RadioButtons = ({context, setContext}) => {

  const BLOCK_RADIO = React.createRef();
  let [widthLong, setWidthLong] = useState(0);
  let [leftLBlock, setLeftLBlock] = useState(null);

  let [leftLong, setLeftLong] = useState(null);

  let styleLong = {
    width: `${widthLong}px`,
    left: `${leftLong - leftLBlock}px`,
  };

  const setCoordinates = (data) => {
    setWidthLong(data.width);
    setLeftLong(data.left);
    setLeftLBlock(BLOCK_RADIO.current.getBoundingClientRect().left);
  };

  return (
    <div className='radio-buttons-block' ref={BLOCK_RADIO}>
        <UserRadioItem
          name='Followed'
          type='followed'
          setContext={setContext}
          context={context}
          setCoordinates={setCoordinates}
        />
        <UserRadioItem
          name='All users'
          type='all'
          setContext={setContext}
          context={context}
          setCoordinates={setCoordinates}
        />
        <div className='radio-long' style={styleLong}></div>
    </div>
  );
};

