import './MessagesChatHeader.css';
import NullAvatar from '../../../../../../../img/NonePhoto.png';
import {NavLink} from "react-router-dom";

export const MessagesChatHeader = ({dataChat}) => {

  return (
    <div className='MessagesChatHeader'>
      <NavLink
        to={`/${dataChat.id}`}
        target="_blank"
        className='MessagesChatHeader-name'
      >
        {dataChat.name}
      </NavLink>
      <NavLink
        to={`/${dataChat.id}`}
        target="_blank"
        className='MessagesChatHeader-photo-block'
      >
        <img src={dataChat.photo !== null ? dataChat.photo : NullAvatar}/>
    </NavLink>
    </div>
  );
};