import API, {API_MESSAGES} from "../../api/api";
import {PageMax, setDialogs, setDialogsPossible, setDialogsSearch, setIsSearch} from "../select_chat_reduser";
import checkSearch from "../../UniversalComponents/scriptSearch/scriptSearch";

export const ThunkTakeFirstDialogsPossible = (pageSize, pageCurrent , term, friend) => {
  return async (dispatch) => {
    const response = await API.takeUsers(pageSize, pageCurrent , term, friend);
    const response_dialogs = await API_MESSAGES.takeDialogs();

    const items = () => response.data.items.filter(item => {
      let context = true;
      response_dialogs.data.forEach(el => {
        if(el.id === item.id) context = false;
      })
      return context;
    })
    dispatch(setDialogsPossible(items()));
  }
};

export const ThunkFirstTakeUsersDialogs = (pageSize, pageCurrent, term, friend) => {
  return async (dispatch) => {
    const response_dialogs = await API_MESSAGES.takeDialogs();
    const response = await API.takeUsers(pageSize, pageCurrent, term, friend);

    const dialogs = (dialogs, term) => dialogs.filter(el => checkSearch(el.userName, term));

    let response_action_dialogs = dialogs(response_dialogs.data, term);

    let response_action = response.data.items.filter(el => {
      let check = true;
      response_action_dialogs.forEach(item => {
        if(el.id === item.id) check = false
      })
      return check;
    })
    dispatch(setDialogsSearch([...response_action_dialogs, ...response_action]));
    dispatch(setIsSearch(true));
  };
};
export const ThunkTakeUsersDialogs = (pageSize, pageCurrent, term, friend) => {
  return async (dispatch) => {
    // dispatch(ToggleLoader(true));
    const response = await API.takeUsers(pageSize, pageCurrent, term, friend);
    dispatch(setDialogsSearch(response.data.items));
    // dispatch(ToggleLoader(false));
  };
};