import {createSlice} from "@reduxjs/toolkit";

let initialState = {
  dataProfile: {
    aboutMe: null,
    contacts: {
      facebook: null,
      github: null,
      instagram: null,
      twitter: null,
      vk: null,
      youtube: null,
    },
    lookingForAJob: null,
    lookingForAJobDescription: null,
    fullName: null,
    userId: null,
    photos: {
      large: null,
      small: null,
    }
  },
  isLoader: true,
  followed: null,
};

const profile_reducer = createSlice({
  name: 'profile_reducer',
  initialState,
  reducers: {
    ToggleLoaderProfile (state, action) {
      state.isLoader = action.payload;
    },
    setUser (state, action) {
      state.dataProfile = action.payload;
    },
    changeStatusFollowedProfile (state, action) {
      state.followed =  action.payload;
    },
    changeStatusProfile (state, action) {
      state.dataProfile.aboutMe = action.payload;
    },
  },
});

export const {
  ToggleLoaderProfile,
  setUser,
  changeStatusFollowedProfile,
  changeStatusProfile
} = profile_reducer.actions;

export default profile_reducer.reducer;
