import {ReadCheck} from "../ReadCheck/ReadCheck";
import {useState} from "react";

export const RenderItemMessagesMy = ({item, onDeleteMassages}) => {
  let transparent = 'transparent';
  let [back, setBack] = useState(transparent);
  let [Design, setDesign] = useState('#0D1117FF');
  let [transition, setTransition] = useState('0s');

  let styleWrapper = {
    background: back,
    transition: transition
  };

  let styleDesign = {
    background: Design,
    transition: transition
  };

  const MouseOver = () => {
    setTransition('0s');
    setBack('#131922');
    setDesign('#131922')
  };
  const MouseOut = () => {
    setTransition('0.3s');
    setBack(transparent);
    setDesign('#0D1117FF')
  };
  const time = new Date(item?.addedAt);

  return (
    <div
      className='renderItemMessages-wrapper'
      onMouseOver={() => MouseOver()}
      onMouseOut={() => MouseOut()}
      onClick={() => onDeleteMassages(item.id)}
      style={styleWrapper}
    >
      <div className='renderItemMessages-block itemMesMy'>
        <p>{item.body}</p>
        <span>{time.getHours() + ":" + time.getMinutes()}</span>
        <ReadCheck context={item.viewed}/>
        <div className='itemMesMy_design_01' ></div>
        <div className='itemMesMy_design_02' style={styleDesign}></div>
      </div>
    </div>
  );
};