import './MainHomeProfileAboutMeBlock.css';
import noPhotoIcon from '../../../../../img/NonePhoto.png';
import GrayBoot from "../../../../../UniversalComponents/GrayBoot/GrayBoot";
import FollowButtonConteiner from "../../../../../UniversalComponents/FollowButtonConteiner/FollowButtonConteiner";
import StatusProfile from "./StatusProfile/StatusProfile";
import {WriteMessage} from "./WriteMessage/WriteMessage";

const MainHomeProfileAboutMeBlock = ({profile, my_id}) => {
  let isLoader = profile.isLoader;
  let dataProfile = profile.dataProfile;

  return (
    <div className='MainHomeProfileAboutMe'>
      <div className='MainHomeProfileAboutMeFotoBlock'></div>
      <div className='MainHomeProfileAboutMeBackLong'>
        <div className='MainHomeProfileAboutMeDataBlock'>
          <div className='MainHomeProfileAboutMeName'>
            {isLoader ? <GrayBoot sizeW={60} sizeH={20}/> : dataProfile.fullName}
          </div>
          <StatusProfile isMy={my_id === dataProfile.userId}
                         isLoader={isLoader}
                         aboutMe={dataProfile.aboutMe}
          />
        </div>
        <div className='MainHomeProfileAboutMeButtonFollowed'>
          {my_id !== dataProfile.userId &&
            <WriteMessage name={dataProfile.fullName}
                         id={dataProfile.userId}
                         photo={dataProfile.photos.small}/>}
          <FollowButtonConteiner name={dataProfile.fullName}
                                 id={dataProfile.userId}
                                 context='profile'
                                 followed={profile.followed}
          />
        </div>
      </div>
      <div className='MainHomeProfileAboutMeFotoBlock'>
        {isLoader ? <GrayBoot /> : <img src={dataProfile.photos.large === null ? noPhotoIcon : dataProfile.photos.large}/>}
      </div>
    </div>
  );
};

export default MainHomeProfileAboutMeBlock;