import './MainHomeUsers.css';
import MainHomeUsersItem from "./MainHomeUsersItem/MainHomeUsersItem";
import React, {useEffect, useState} from "react";
import Preloader from "../../../../UniversalComponents/Preloader/Preloader";
import InputSearch from "../../../../UniversalComponents/InputSearch/InputSearch";
import {useDispatch, useSelector} from "react-redux";
import {ThunkFirstTakeUsers, ThunkTakeUsers} from "../../../../redux/thunks/thunk_users";
import {setInitialState, setScroll, setTerm} from "../../../../redux/users_reducer";
import {RadioButtons} from "./HeaderUser";
import {useLocation, useNavigate} from "react-router";
import {parsingURL} from "./parsingURL";

const MainHomeUsers = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const users = useSelector(state => state.users);

  const [context, setContext] = useState(users.dataScroll.cache ? users.dataScroll.section
                                                                         : parsingURL(location.search).section || 'followed');
  const friend = context !== 'all';

  const TakeUsers = () => dispatch(ThunkTakeUsers(users.pageSize, users.pageCurrent, users.term, friend));
  const callBackSearch = (term) => dispatch(setTerm(term));

  useEffect(() => {
    !users.dataScroll.cache && dispatch(ThunkFirstTakeUsers(users.pageSize, 1, users.term, friend));
    navigate(`?section=${context}`);
  }, [users.term, context]);

  //scroll
  const SCROLL_BLOCK = React.createRef();
  let [scrollFixed, setScrollFixed] = useState(users.dataScroll.scroll);

  useEffect(() => {
    SCROLL_BLOCK.current.scrollTop = scrollFixed;
    !users.dataScroll.cache && dispatch(setInitialState());
    !users.dataScroll.cache && dispatch(setTerm(''));
    dispatch(setScroll({
      cache: false,
      scrollTop: 0,
      section: users.dataScroll.section,
    }));
  }, []);

  const saveScroll = () => dispatch(setScroll({
      cache: true,
      scrollTop: SCROLL_BLOCK.current.scrollTop,
      section: parsingURL(location.search).section
  }));;

  return (
    <div className='MainHomeUsers HomeRightBlock'
         ref={SCROLL_BLOCK}>
      <div className='header-users-radio-block' >
        <RadioButtons context={context} setContext={setContext}/>
      </div>
      <div className='users-search-block'>
        <InputSearch
          placeholder='Find a user'
          Style='users-search'
          valueInitial={users.term}
          callBack={callBackSearch}
          delay={500}
        />
        <div className='Found'>
          <p>Found:</p>
          {users.isLoader ? <Preloader type='circle'/> : <span>{users.totalCount}</span>}
        </div>
      </div>
      <div className='MainHomeUsersBlock' >
        {users.usersAll.map(itemState => <MainHomeUsersItem saveScroll={saveScroll} key={itemState.id} itemState={itemState} />)}
      </div>
      <div className='showMore'>
        {users.isLoader && <Preloader type='circle'/>}
        {users.totalCount === 0 && <div className='not-users'>Nothing found for: <span>{users.term}</span></div>}
        {users.totalCount !== 0 &&
          users.pageMax >= users.pageCurrent &&
          !users.isLoader &&
          <button className='BtnGreen MainHomeUsersBtnBlock' onClick={() => TakeUsers()}>Show more</button>
        }
      </div>
    </div>
  );
};

export default MainHomeUsers;