export const errorAuth = (errors, wrongLoginAuth) => {
  let errorsAction = {};
  if(wrongLoginAuth === "Incorrect Email or Password") {
    errorsAction = {
      email: 'Wrong login or password',
      password: 'Wrong login or password'
    };
  };

  if(errors.email !== undefined) {
    errorsAction.email = errors.email
  };
  if(errors.password !== undefined) {
    errorsAction.password = errors.password
  };

  return errorsAction;
};