import './MainHome.css';
import {Route, Routes} from "react-router-dom";
import MainHomeBack from "./MainHomeBack/MainHomeBack";
import MainHomeMessages from "./MainHomeMessages/MainHomeMessages";
import AuthHoc from "../../../UniversalComponents/AuthHoc/AuthHoc";
import MainHomeUsers from "./MainHomeUsers/MainHomeUsers";
import Settings from "./Settings/Settings";
import MainHomeProfile from "./MainHomeProfile/MainHomeProfile";

const MainHome = () => {
  let AuthMainHomeProfile = AuthHoc(MainHomeProfile);
  let AuthMainHomeBack = AuthHoc(MainHomeBack);
  let AuthMainHomeMessages = AuthHoc(MainHomeMessages);
  let AuthMainHomeUsers = AuthHoc(MainHomeUsers)
  let AuthSettings = AuthHoc(Settings);

  return (
    <div className='MainHome'>
      <Routes>
        <Route path='/:id' element={<AuthMainHomeProfile />} />
        <Route path='/news' element={<AuthMainHomeBack />} />
        <Route path='/messages/*' element={<AuthMainHomeMessages />} />
        <Route path='/users' element={<AuthMainHomeUsers />} />
        <Route path='/settings' element={<AuthSettings />} />
      </Routes>
    </div>
  );
};

export default MainHome;