import {Form, Formik} from "formik";
import {validateSingUp} from "../validate/validate";
import {FormAuthItemPassword} from "../FormAuthItem/FormAuthItemPassword";
import {FormAuthItem} from "../FormAuthItem/FormAuthItem";

const FormSingUp = () => {
  const submitSingUp = ({ setSubmitting }) => {
    setSubmitting(false)
  };

  return (
    <Formik
      initialValues={{
        name: '',
        email: '',
        password: '',
        password_2: '',
      }}
      onSubmit={submitSingUp}
      validate={validateSingUp}
    >
      {({ isSubmitting, errors,touched}) => {
        return (
          <Form className='form-sign'>
            <FormAuthItem
              errors={errors.name}
              touched={touched.name}
              name='name'
              type='text'
              placeholder='name'
            />
            <FormAuthItem
              errors={errors.email}
              touched={touched.email}
              name='email'
              type='email'
              placeholder='email'
            />
            <FormAuthItemPassword
              errors={errors.password}
              touched={touched.password}
               name='password'
               type='password'
               placeholder='password'
            />
            <FormAuthItemPassword
              errors={errors.password_2}
              touched={touched.password_2}
               name='password_2'
               type='password'
               placeholder='password'
            />
            <button className='BtnGreen' type="submit" disabled={isSubmitting}>Sing in</button>
          </Form>
        )}}
    </Formik>
  );
};

export default FormSingUp;