import './Preloader.css';
import React from "react";

const Preloader = ({type, className = '', context}) => {

  let main = type => {
    switch (type) {
      case 'circle': return <div className="TYPE_1_0"><div className="TYPE_1_1"><div></div></div></div>;
    }
  }

  return (
    <div
      className={`Preloader ${className}`}
      style={{
        position: context === 'absolute' ? 'absolute' : 'relative'
      }}
    >{main(type) }</div>
  )
}

export default Preloader;