import {createSlice} from "@reduxjs/toolkit";

let initialState = {
  dialogs: [],
  dialogsPossible: [],
  Search: {
    dialogsSearch: [],
    isSearch: false,
  },
  pageSize: 20, //max 20
  pageCurrent: 1,
  pageMax: 0,
};

const select_chat_reducer = createSlice({
  name: 'select_chat_reducer',
  initialState,
  reducers: {
    setIsSearch (state, action) {
      state.Search.isSearch = action.payload;
    },
    setDialogsPossible (state, action) {
      state.dialogsPossible = action.payload;
    },
    setDialogsPossibleNull (state) {
      state.dialogsPossible = [];
    },
    setDialogs (state, action) {
      state.dialogs = action.payload;
    },
    setDialogsSearch (state, action) {
      state.Search.dialogsSearch = action.payload;
    },
  }
});

export const {
  setDialogsPossible,
  setDialogs,
  setTerm,
  PageMax,
  setDialogsSearch,
  setIsSearch,
} = select_chat_reducer.actions;

export default select_chat_reducer.reducer;