import './App.css';
import {Route, Routes} from "react-router-dom";
import AuthHoc from "./UniversalComponents/AuthHoc/AuthHoc";
import Home from "./Components/Home/Home";
import NotificationsErrorsContainer from "./Components/NotificationsContainer/NotificationsContainer";
import {useEffect} from "react";
import {useDispatch} from "react-redux";
import {ThunkSetMeProfile} from "./redux/thunks/thunk_profile";
import Auth from "./Components/Auth/Auth";

const App = () => {
  const dispatch = useDispatch();

  const forAuth = () => {
   dispatch(ThunkSetMeProfile())
   setTimeout(() => forAuth(), 10000);
  };

  useEffect(() => {
    forAuth();
  }, []);

  let AuthHome = AuthHoc(Home);

  return (
    <div className="App">
      <Routes>
        <Route path='/*' element={<AuthHome/>} />
        <Route path='/login' element={<Auth context='login'/>} />
        <Route path='/registration' element={<Auth context='registration'/>} />
      </Routes>
      <NotificationsErrorsContainer />
    </div>
  );
}

export default App;