import {ErrorMessage, Field} from "formik";
import './FormAuthItem.css';
import {useState} from "react";
import {ErrorBlock} from "./ErrorBlock";

export const FormAuthItem = ({name, type, errors, touched, placeholder}) => {
  return (
    <div className='label'>
      <Field
        type={type}
        name={name}
        placeholder={placeholder}
        className={`form-sign-input ${(errors !== undefined && touched) && 'inputRedBorder'}`}
      />
      <ErrorBlock errors={errors} touched={touched}/>
    </div>
  );
};



