import './MessagesSelectItem.css'
import {NavLink} from "react-router-dom";
import nonePhoto from './../../../../../../../img/NonePhoto.png';

const MessagesSelectItem = ({itsState}) => {
  let newMessages = itsState.newMessagesCount === 0;

  const time = new Date(itsState?.lastDialogActivityDate);

  return (
    <NavLink to={`?sel=${itsState.id}`} className='MessagesSelectItem'>
      <div className='MessagesSelectItemBlockPhoto'>
        <img src={itsState.photos.small !== null ? itsState.photos.small : nonePhoto} />
      </div>
      <div className='MessagesSelectItemBlockData'>
        <div className='MessagesSelectItemNameTimeBlock'>
            <p className='MessagesSelectItemName'>{itsState.userName}</p>
            <p className='MessagesSelectItemTime'>{time ? time.getHours() + ":" + time.getMinutes() : ''}</p>
          </div>
        {<div className='MessagesSelectItemLastMessage'>
          {!newMessages ? <p style={{fontWeight: 900, color: 'white'}}>message</p>
            : <p style={{color: '#ffffff8a', opacity: 0.7}}>message</p>}
          {!newMessages && <div className='MessagesSelectItemLastMessage-new'>{itsState.newMessagesCount}</div>}
        </div>}
      </div>
    </NavLink>
  )
}

export default MessagesSelectItem;