import './MessagesSelectBlock.css';
import InputSearch from "../../../../../UniversalComponents/InputSearch/InputSearch";
import MessagesSelectMain from "./MessagesSelectMain/MessagesSelectMain";
import {useDispatch, useSelector} from "react-redux";
import {ThunkTakeDialogs, ThunkTakeNewMessages} from "../../../../../redux/thunks/thunk_messages";
import {useEffect} from "react";
import {parsingURL} from "../../MainHomeUsers/parsingURL";
import {useLocation} from "react-router";
import {
  ThunkFirstTakeUsersDialogs,
  ThunkTakeFirstDialogsPossible
} from "../../../../../redux/thunks/thunk_massages_sel_friend";
import {
  setDialogsPossible,
  setDialogsSearch,
  setIsSearch
} from "../../../../../redux/select_chat_reduser";

const MessagesSelectBlock = ({currentWidth}) => {
  const dispatch = useDispatch();
  const location = useLocation();

  const {
    dialogs, dialogsPossible,
    Search, pageSize, pageCurrent
  } = useSelector(state => state.select_chat);
  const urlId = parsingURL(location.search).sel;

  const TakeDialogs = () => {
    dispatch(ThunkTakeDialogs(urlId))
  };

  const takeSearchDialogs = (text) => {
    if(text.length !== 0) {
      dispatch(ThunkFirstTakeUsersDialogs(Search.pageSize, 1, text, true))
    } else {
      dispatch(setDialogsSearch([]));
      dispatch(setIsSearch(false));
    }
  };

  useEffect(() => {
    TakeDialogs();
    dispatch(ThunkTakeFirstDialogsPossible(pageSize, 1 , '', true));
    return () => {
      dispatch(setDialogsSearch([]));
      dispatch(setDialogsPossible([]));
      dispatch(setIsSearch(false));
    }
  }, []);

  return (
    <div className='MessagesSelectBlock' style={{minWidth: currentWidth + 'px'}}>
      <div className='MessagesSelectHeader'>
        <InputSearch
          placeholder='Find a chat'
          callBack={takeSearchDialogs}
          delay={300}
        />
      </div>
      <MessagesSelectMain
        dialogsSearch={Search.dialogsSearch}
        dialogs={dialogs}
        dialogsPossible={dialogsPossible}
      />
    </div>
  )
}

export default MessagesSelectBlock;